  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.Navabar1{
  width: 30%;
}
.lineHeight1{
  line-height: 43px;
}
.profile1{
  margin-top: 20%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Button1{
  display: flex;
  gap: 115px;
  align-items: center;
}
#resume-button-1{
  width: 110px;
  height: 41px;
    background-color: greenyellow;
    border: none;
    font-weight: bold;
